const galleryEl = document.querySelector('ul.gallery');

const galleryElements = images.map(image => {
  const galleryItemEl = document.createElement('li');
  galleryItemEl.classList.add('gallery-item');

  const galleryLinkEl = document.createElement('a');
  galleryLinkEl.classList.add('gallery-link');
  galleryLinkEl.setAttribute('href', image.original);

  const imageEl = document.createElement('img');
  imageEl.classList.add('gallery-image');
  imageEl.setAttribute('alt', image.description);
  imageEl.setAttribute('src', image.preview);
  imageEl.setAttribute('data-source', image.original);

  galleryItemEl.append(galleryLinkEl);

  galleryLinkEl.append(imageEl);

  return galleryItemEl;
});

galleryEl.append(...galleryElements);

new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionDelay: 250,
});
